import 'antd/dist/antd.css';
import { Provider } from 'mobx-react';
import DevTools from 'mobx-react-devtools';
import React, { Component, Fragment } from 'react';
import { Router, Redirect, Route, Switch } from 'react-router-dom';
import { config } from 'utils/request';
import './App.css';
import { message } from 'antd';
import SettingsStore from './stores/Settings';
import UserStore from './stores/User';
//pages
import Login from 'pages/Login';
import Home from 'pages/Home';
import Article from 'pages/Article';
import ArticleDetail from 'pages/Article/detail';
import Test from './test';
import Layout from 'modules/Layout';
import { createBrowserHistory } from 'history';
export const history = createBrowserHistory({
  basename: '/',
});
const user = new UserStore();
const settings = new SettingsStore();

config
  .url((url) => `${process.env.PUBLIC_URL}${settings.apiServer}${url}`)
  .tokenInvalid(() => {
    message.warn('登陆过期，请重新登陆');
    history.push('/login');
  });

class App extends Component {
  render() {
    return (
      <Provider user={user} settings={settings}>
        <Fragment>
          {process.env.NODE_ENV === 'development' ? <DevTools noPanel /> : null}
          <Router history={history}>
            <Switch>
              <Route exact path="/login" component={Login} />
              <Layout>
                <Switch>
                  <Redirect exact from="/" to="/home" />
                  <Route path="/home" component={Home} />
                  <Route exact path="/article" component={Article} />
                  <Route exact path="/test" component={Test} />
                  <Route path="/article/detail" component={ArticleDetail} />
                </Switch>
              </Layout>
            </Switch>
          </Router>
        </Fragment>
      </Provider>
    );
  }
}

export default App;
