import React from 'react';
import { Table } from 'antd';
import { TableProps } from 'antd/lib/table';
interface IProps extends TableProps<any> {
  pageSize?: number;
  pageNum?: number;
  total?: number;
}
export default (props: IProps) => {
  let { dataSource, pageSize, pageNum, total, pagination, ...rest } = props;
  const paginationProps = {
    current: pageNum,
    total,
    pageSize,
  };
  pagination = pagination === false ? pagination : paginationProps;
  dataSource.forEach((item) => (item.key = item._id));
  return <Table {...rest} dataSource={dataSource} pagination={pagination} />;
};
