import React, { useEffect, useState, useCallback } from 'react';
import { Link, useHistory } from 'react-router-dom';
import Title from 'components/Title';
import TableList from 'components/TableList';
import A from 'components/A';
import api from 'api';
import dict from 'const/dict';
import { Button, message } from 'antd';
import ImageView from 'components/ImageView';
import { UTCtoDateString } from 'utils/date';
export default () => {
  const [pagination, setPagination] = useState({
    pageSize: 10,
    pageNum: 1,
  });
  const [dataSource, setDataSource] = useState([]);
  const [loading, setLoading] = useState(false);
  const [dataTotal, setDataTotal] = useState(0);
  const history = useHistory();
  const { pageSize, pageNum } = pagination;

  //获取列表
  const fetchList = useCallback(async () => {
    setLoading(true);
    const { data, code } = await api.article.list({ pageSize, pageNum });
    setLoading(false);
    if (code === 10000) {
      setDataSource(data.list);
      setDataTotal(data.total);
    }
  }, [pageSize, pageNum]);
  const delArtilce = async (_id) => {
    const { code } = await api.article.delArticle({ _id });
    if (code === 10000) {
      message.success('删除成功');
      fetchList();
    }
  };
  const onTableChange = (p) => {
    setPagination({ pageSize: p.pageSize, pageNum: p.current });
  };
  const columns = [
    {
      title: '标题',
      dataIndex: 'title',
      key: 'title',
    },
    {
      title: '修改时间',
      key: 'date',
      render: (r) => {
        return UTCtoDateString(r.date);
      },
    },
    {
      title: '封面图',
      key: 'logoUrl',
      ellipsis: true,
      render: (r) => {
        console.log(r.logoUrl);
        return r.logoUrl ? <ImageView src={r.logoUrl} /> : null;
      },
    },
    {
      title: '作者',
      key: 'author',
      render: (r) => {
        console.log(r);
        return r.author.username;
      },
    },
    {
      title: '类型',
      key: 'category',
      render: (r) => {
        return dict.articleCategory[r.category];
      },
    },
    {
      title: '状态',
      key: 'status',
      render: (r) => {
        return dict.articleStatus[r.status];
      },
    },
    {
      title: '操作',
      key: 'handle',
      render: (r) => {
        return (
          <>
            <Link to={`/article/detail?id=${r._id}`}>编辑</Link>
            <A onClick={() => delArtilce(r._id)}>删除</A>
          </>
        );
      },
    },
  ];
  useEffect(() => {
    fetchList();
  }, [fetchList]);
  return (
    <>
      <Title title="文章列表" />
      <Button
        type="primary"
        icon="plus"
        onClick={() => history.push('/article/detail')}
        style={{ marginBottom: 20 }}
      >
        创建文章
      </Button>
      <TableList
        onChange={onTableChange}
        loading={loading}
        columns={columns}
        dataSource={dataSource}
        pageNum={pageNum}
        pageSize={pageSize}
        total={dataTotal}
      />
    </>
  );
};
