import { Avatar, Dropdown, Icon, Layout, Menu } from 'antd';
import { inject, observer } from 'mobx-react';
import React from 'react';
import { RouteComponentProps, withRouter } from 'react-router';
import styles from './index.module.scss';
import api from 'api';
const { Header } = Layout;
/**
 * 页头
 */
// @ts-ignore
@withRouter
@inject('settings')
@observer
export default class extends React.Component<RouteComponentProps> {
  state = { collapsed: false };

  onMenuClick = async ({ key }) => {
    const { history } = this.props;
    if (key === 'logout') {
      // logout
      let { code } = await api.user.logout();
      if (code === 10000) {
        history.push('/login');
      }
    }
  };

  onToggleCollapse = () => {
    // @ts-ignore
    let { settings } = this.props;
    settings.collapsed = !settings.collapsed;
  };
  render() {
    const menu = (
      <Menu
        className={styles.menu}
        selectedKeys={[]}
        onClick={this.onMenuClick}
      >
        <Menu.Item key="logout">
          <Icon type="logout" />
          退出登录
        </Menu.Item>
      </Menu>
    );
    // @ts-ignore
    let { settings } = this.props;

    return (
      <Header
        style={{
          padding: 0,
        }}
      >
        <div className={styles.header}>
          <span className={styles.trigger} onClick={this.onToggleCollapse}>
            <Icon type={settings.collapsed ? 'menu-unfold' : 'menu-fold'} />
          </span>
          <Dropdown overlay={menu} overlayClassName={styles.overlay}>
            <span className={styles.account}>
              <Avatar
                size="small"
                className={styles.avatar}
                src="https://gw.alipayobjects.com/zos/antfincdn/XAosXuNZyF/BiazfanxmamNRoxxVxka.png"
                alt="avatar"
              />
              <span className={styles.name}>白居易</span>
            </span>
          </Dropdown>
        </div>
      </Header>
    );
  }
}
