import React, { useState, useEffect, useCallback } from 'react';
import { useHistory } from 'react-router-dom';
import { Form, Input, Button, Select, Spin, DatePicker } from 'antd';
import { FormComponentProps } from 'antd/es/form';
import { formItemLayout, tailFormItemLayout } from 'const/layout';
import { articleStatusList, articleCategoryList } from 'const/enum';
import api from 'api';
import { getUrlParams } from 'utils';
import { Article, createArticle } from 'models';
import Markdown from 'components/Markdown';
import moment from 'moment';
const { Option } = Select;
interface IProps extends FormComponentProps {}
type Author = {
  username: string;
  _id: string;
};
const FormDetail = (props: IProps) => {
  const [detailLoading, setDetailLoading] = useState(false);
  const [authorList, setAuthorList] = useState<Array<Author>>([]);
  const [article, setArticle] = useState<Article>(createArticle());
  const {
    title,
    keyword,
    summary,
    logoUrl,
    author,
    status,
    category,
    link,
    content,
    date,
  } = article;
  const history = useHistory();
  let _id = getUrlParams('id');
  // 获取作者列表
  const fetchAuthorList = async () => {
    let { code, data } = await api.user.getAuthors();
    if (code === 10000) {
      setAuthorList(data);
    }
  };
  //获取详情
  const fetchDetail = useCallback(async () => {
    setDetailLoading(true);
    let { code, data } = await api.article.getDetail({ id: _id });
    if (code === 10000) {
      setDetailLoading(false);
      setArticle(data);
    }
  }, [_id]);
  useEffect(() => {
    fetchAuthorList();
    if (_id) {
      fetchDetail();
    }
  }, [_id, fetchDetail]);
  // 提交表单
  const handleSubmit = (e) => {
    e.preventDefault();
    props.form.validateFields(async (err, values) => {
      if (!err) {
        let { code } = await api.article.addArticle({
          ...values,
          ...(_id && { _id }),
        });
        if (code === 10000) {
          history.goBack();
        }
      }
    });
  };
  const {
    form: { getFieldDecorator },
  } = props;
  return (
    <Spin spinning={detailLoading}>
      <Form {...formItemLayout} onSubmit={handleSubmit}>
        <Form.Item label="标题">
          {getFieldDecorator('title', {
            initialValue: title,
            rules: [
              {
                message: '标题不能为空',
                required: true,
              },
            ],
          })(<Input />)}
        </Form.Item>
        <Form.Item label="关键字">
          {getFieldDecorator('keyword', { initialValue: keyword })(<Input />)}
        </Form.Item>
        <Form.Item label="概述">
          {getFieldDecorator('summary', {
            initialValue: summary,
            rules: [
              {
                message: '概述不能为空',
                required: true,
              },
            ],
          })(<Input />)}
        </Form.Item>
        <Form.Item label="封面链接">
          {getFieldDecorator('logoUrl', {
            initialValue: logoUrl,
          })(<Input />)}
        </Form.Item>
        <Form.Item label="作者">
          {getFieldDecorator('author', {
            initialValue: author,
            rules: [{ required: true, message: '请选择作者' }],
          })(
            <Select placeholder="选择作者">
              {authorList.map((item) => (
                <Option key={item._id} value={item._id}>
                  {item.username}
                </Option>
              ))}
            </Select>
          )}
        </Form.Item>
        <Form.Item label="修改时间">
          {getFieldDecorator('date', {
            initialValue: date ? moment(date) : moment(new Date()),
            // rules: [{ required: true, message: '修改时间必填' }],
          })(
            <DatePicker
              style={{ width: '100%' }}
              format={'YYYY-MM-DD HH:mm:ss'}
            />
          )}
        </Form.Item>

        <Form.Item label="状态">
          {getFieldDecorator('status', {
            initialValue: status,
            rules: [{ required: true, message: '请选择文章状态' }],
          })(
            <Select placeholder="请选择文章状态">
              {articleStatusList.map((item) => (
                <Option key={item.status} value={item.status}>
                  {item.name}
                </Option>
              ))}
            </Select>
          )}
        </Form.Item>
        <Form.Item label="文章类型">
          {getFieldDecorator('category', {
            initialValue: category,
            rules: [{ required: true, message: '请选择文章类型' }],
          })(
            <Select placeholder="请选择文章类型">
              {articleCategoryList.map((item) => (
                <Option key={item.value} value={item.value}>
                  {item.name}
                </Option>
              ))}
            </Select>
          )}
        </Form.Item>
        <Form.Item
          label="内容"
          wrapperCol={{ xs: { span: 24 }, sm: { span: 18 } }}
        >
          {getFieldDecorator('content', {
            initialValue: content,
            rules: [{ required: true, message: '内容不能为空' }],
          })(<Markdown />)}
        </Form.Item>
        <Form.Item {...tailFormItemLayout}>
          <div className="btn-group">
            <Button type="primary" htmlType="submit">
              保存
            </Button>
            <Button onClick={() => history.goBack()}>返回</Button>
          </div>
        </Form.Item>
      </Form>
    </Spin>
  );
};
export default Form.create<IProps>()(FormDetail);
