import { Icon, Menu } from 'antd';
import React, { Component } from 'react';
import { Link, withRouter, RouteComponentProps } from 'react-router-dom';
import MenuConfig from './config';
// import { observer,inject} from 'mobx-react';
interface MenuItemProps {
  icon?: string;
  title?: string;
}
interface SiderMenuProps extends Partial<RouteComponentProps> {
  collapsed?: boolean;
}
interface IState {
  openKeys?: string[];
  selectedKeys?: string[];
}
class MenuTitle extends Component<MenuItemProps> {
  render() {
    let { title, icon } = this.props;
    return (
      <span>
        <Icon type={icon} />
        <span>{title}</span>
      </span>
    );
  }
}
//@ts-ignore
@withRouter
// @inject('user')
// @observer
export default class SiderMenu extends Component<SiderMenuProps, IState> {
  constructor(props) {
    super(props);
    const pathname = props.location.pathname;
    const openKeys = [pathname.split('/')[1]];
    this.state = {
      openKeys,
      selectedKeys: ['']
    };
  }
  componentDidMount() {
    this.props.history.listen(location => {
      const pathname = location.pathname;
      this.setSelectedKeys(pathname);
    });
    const { pathname } = this.props.location;
    this.setSelectedKeys(pathname);
  }
  setSelectedKeys = pathname => {
    let pathArr = pathname.split('/');
    let selectedKeys = [];
    pathArr.reduce((prev, current) => {
      let item = prev + '/' + current;
      selectedKeys.push(item);
      return item;
    });
    this.setState({
      selectedKeys
    });
  };
  renderMenu = config => {
    let { history } = this.props;
    return config.map(item => {
      const { children, key, icon, name, isTitle } = item;
      const Sub = isTitle ? (
        <Menu.Item onClick={() => history.push(key)} key={key}>
          <Icon type={icon} />
          <span>{name}</span>
        </Menu.Item>
      ) : (
        <Menu.Item key={key}>
          <Link to={key}>{name}</Link>
        </Menu.Item>
      );
      return children ? (
        <Menu.SubMenu key={key} title={<MenuTitle icon={icon} title={name} />}>
          {this.renderMenu(children)}
        </Menu.SubMenu>
      ) : (
        Sub
      );
    });
  };
  processMenu = (list = MenuConfig) => {
    let result = list.filter(item => {
      let { children } = item;
      if (children) {
        //@ts-ignore
        item.children = this.processMenu(children);
      }
      return true;
    });
    return result;
  };
  onOpenChange = openKeys => {
    const latestOpenKey = openKeys.find(
      key => this.state.openKeys.indexOf(key) === -1
    );
    this.setState({
      openKeys: latestOpenKey ? [latestOpenKey] : []
    });
  };
  render() {
    // @ts-ignore
    let { collapsed } = this.props;
    let { openKeys, selectedKeys } = this.state;
    let menuList = this.processMenu();
    return (
      <Menu
        key="Menu"
        mode="inline"
        inlineCollapsed={collapsed}
        openKeys={openKeys}
        selectedKeys={selectedKeys}
        onOpenChange={this.onOpenChange}
        theme="dark"
        style={{ padding: '16px 0', width: '100%' }}
      >
        {this.renderMenu(menuList)}
      </Menu>
    );
  }
}
