import request from 'utils/request';
export default {
  addArticle(options) {
    return request('/admin/article/add', options);
  },
  delArticle(options) {
    return request('/admin/article/del', options);
  },
  list(options) {
    return request('/admin/article/list', options, { method: 'GET' });
  },
  getDetail(options) {
    return request('/admin/article/detail', options, { method: 'GET' });
  },
};
